import React,{ useState, useEffect }from "react";
import { Link } from 'react-router-dom';
import axios from "axios";


function Visits() {
  const [data, setData] = useState(null);
  useEffect(() => {
    axios.get(`http://stronglasstough.com/velan/api.php?getVisits`).then((response) => {
      console.debug(response);
      setData(response.data);
    });
  }, []);

  return (
    <div>
      <div className="page-header">
        <h3 className="page-title"> Visits </h3>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item">
              <a href="!#" onClick={(event) => event.preventDefault()}>
                App
              </a>
            </li>
            <li className="breadcrumb-item active" aria-current="page">
              Visits
            </li>
          </ol>
        </nav>
      </div>
      <div className="row">
        <div className="col-lg-12 grid-margin stretch-card">
          <div className="card">
            <div className="card-body">
              <div className="table-responsive">
                <table className="table table-bordered">
                  <thead>
                    <tr>
                      <th> # </th>
                      <th> Name </th>
                      <th> Address </th>
                      <th> Mobile </th>
                      <th> Buyer Type </th>
                      <th> Buyer Name </th>
                      <th> Buyer Mobile </th>
                      <th> Location </th>
                      <th> Branch </th>
                      <th> Detail </th>
                    </tr>
                  </thead>
                  <tbody>
                    {data?.visits.map((v, i) => (
                      <React.Fragment key={i}>
                        <tr>
                          <td> {i + 1} </td>
                          <td> {v.customer_name} </td>
                          <td>{v.customer_address}</td>
                          <td> {v.customer_mobile_number} </td>
                          <td>{v.person_type}</td>
                          <td> {v.buying_person_name} </td>
                          <td> {v.buying_person_mobile_number} </td>
                          <td> {v.building_area} </td>
                          <td> {v.branch} </td>
                          <td> <Link className="btn btn-primary" to={{pathname: `/pages/visit`, search: `?id=${v.id}`}}>Detail</Link></td>
                        </tr>
                      </React.Fragment>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Visits;
